body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__circle {
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: handmove;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform: rotate(0);
}

.stopwatch__seconds--position {
  position: absolute;
  bottom: 40vmin;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: handmove;
  animation-duration: 600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  transform: rotate(0);
}

.stopwatch__minutes--position {
  position: absolute;
  bottom: 40vmin;
}

@keyframes handmove {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
